<template>
  <div 
    class="my-options"
    :class="`align-${align} ${size}`"
  >
      <select 
        v-model="selected" 
        :name="id" 
        :id="id"
       >
          <option value="">{{ defOption }}</option>
          <option  
            v-for="(dt,index) in data" 
            :key="index" 
            :value="dt._id">{{ dt.name }}</option>
      </select>
  </div>
</template>

<script>
export default {
    name : 'myOptions',
    props : {
        id : {
            type : String,
            default : "Unknown"
        },
        data : {
            type : Array,
            default : () => [],
        },
        align : {
            type : String,
            default : 'left'
        },
        defOption : {
            type  : String,
            default : 'Choose',
        },
        size : {
            type : String,
            default : 'md'
        }
    },
    data() {
        return {
            selected : ""
        }
    },
    watch : {
        selected  : {
            handler : function (val) {
                this.$emit('optionsChange',val)
            }
        }
    }
} 
</script>

